@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  li {
    @apply mx-2 font-medium hover:text-orange-400 hover:cursor-pointer;
  }
  button {
    @apply border border-cyan-400 rounded p-2 hover:bg-cyan-400 hover:text-white transition-all delay-100 font-medium shadow-lg;
  }
}

.subscribe{
  width: 900px;
  position: fixed;
  top: 55%;
  left: 50%;
  transform: translate(-50% , -50%);
  z-index: 9999999;
}

@media (max-width : 999px) {
  .subscribe {
    width: 70%;
  }
}